.caixa-itens {
    width: 100%;
    height: 50px;
    position: relative;
    float: left;
    line-height: 50px;
    color: #edecec;
    transition-duration: .5s;
    border-bottom: 1px solid rgba(245, 245, 245, .2);
    padding-left: 10px;
}

.caixa-itens:hover{
    background-color: skyblue;
    transition-duration: .5s;
    color: darkblue;
    cursor: pointer;
}